body {
    margin: 0;
    padding: 0;
    background-color: white;
    color: rgb(60, 158, 143);
    font-family: Arial, Helvetica, sans-serif;
    text-decoration: none
}

.wrapper {
    color: rgb(60, 158, 143);
    width: 100vw
}

.wrapper ul li {
    padding-top: 5px;
}

#nav {
    display: flex;
    justify-content: center;
    font-size: 36px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    border-bottom: 2px solid;
    color: #4ABDAC;
    margin: 0;
    padding: 0;
    text-decoration: none;
}


#nav #nav-links li {
    list-style: none;
    text-align: center;
    border-radius: 10px;
    display: inline;
    padding: 10px;
    color: #4ABDAC
}

@media only screen and (max-width: 768px) {

    #nav {
        justify-content: center;
    }
    #nav #nav-links li {
        flex-wrap: wrap;
        font-size: 20px;
        padding: 10px;
        margin: 0px;
    }

}


a {
    padding: 10px;
    text-decoration: none;
    color: rgb(60, 158, 143)
}



.active {
    background-color: #4ABDAC;
    border-radius: 5px;
    color:white;
    border-bottom: 6px solid rgb(39, 99, 90);
    transition: 0.4s;
}

a:active {
    color: #4ABDAC;
}

h1 {
    font-size: 36px;
}

h3 {
    font-size: 28px;
}

p {
    font-size: 18px;
}



.fade-enter {
    opacity: 0.1
}

.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 250ms ease-in;
  }

.fade-exit {
    opacity: 1;
  }

.fade-exit.fade-exit-active {
    opacity: 0.01;
    transition: opacity 250ms ease-in;
  }

div.transition-group {
    position: relative;
  }

section.route-section {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
  }

